import React, { useState } from "react";

export default function Textviwe(props) {
  const handelonclicku = () => {
    text = text.toUpperCase();
    setText(text);
  };
  const handelonclickl = () => {
    text = text.toLowerCase();
    setText(text);
  };
  const handelonclick3 = () => {
    text = "";
    setText(text);
  };
  const handelonchange = (event) => {
    setText(event.target.value);
  };

  let vr = () => {
    return text === "" ? 0 : text.length;
  };

  let vrr = () => {
    let a = 0;
    let NewText = text.trim();
    NewText = NewText + " ";
    for (let i = 1; i < NewText.length - 1; i++) {
      if (NewText.charAt(i) !== " " && NewText.charAt(i + 1) === " ") {
        a++;
      }
    }
    return a;
  };

  let [text, setText] = useState("");
  return (
    <div className="container">
      <div
        className={`mb-3 text-${props.Curmode === "light" ? "dark" : "light"}`}
      >
        <label
          for="exampleFormControlTextarea1"
          className="form-label"
          colr={`${props.Curmode}==="light"?"black":"white"`}
        >
          Manupulate The Text
        </label>
        <textarea
          className="form-control"
          id="exampleFormControlTextarea1"
          rows="8"
          onChange={handelonchange}
          value={text}
        ></textarea>
        <button
          type="button"
          class="btn btn-primary my-2"
          onClick={handelonclicku}
          value={text}
        >
          To Upper
        </button>
        <button
          type="button"
          class="btn btn-primary my-2 mx-2"
          onClick={handelonclickl}
        >
          To Lower
        </button>
        <button
          type="button"
          class="btn btn-primary my-2 mx-1"
          onClick={handelonclick3}
        >
          Clear
        </button>
        <br></br>
        <p1>
          {vrr()} total words,{vr()} Total length
        </p1>
      </div>
    </div>
  );
}
