import React from "react";
import { Link } from "react-router-dom";

export default function NavBar(props) {
  // const te = (ele) => {
  //   let text = "navbar navbar-expand-lg navbar-dark bg-dark";
  //   let text1 = "navbar navbar-expand-lg navbar-light bg-light";
  //   if (ele === "Light") {
  //     return text1;
  //   } else {
  //     return text;
  //   }
  // };
  return (
    <div className="continer">
      {/* <nav className={te(props.mode)}> */}
      <nav
        className={`navbar navbar-expand-lg navbar-${props.Curmode} bg-${props.Curmode}`}
      >
        <div className="container-fluid">
          <a className="navbar-brand" href="/">
            TextManuplator
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/About">
                  About
                </Link>
              </li>
            </ul>
            <div
              className={`form-check form-switch text-${
                props.Curmode === "light" ? "dark" : "light"
              }`}
            >
              <input
                className="form-check-input"
                type="checkbox"
                id="flexSwitchCheckDefault"
                onClick={props.ToggleMode}
              />
              <label
                className="form-check-label"
                htmlFor="flexSwitchCheckDefault"
              >
                DarkMode
              </label>
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
}
