import "./App.css";
import NavBar from "./components/NavBar.js";
import Textviwe from "./components/Textviwe";
import About from "./components/About";

import React, { useState } from "react";
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";

function App() {
  const togglebtn = () => {
    if (mode === "light") {
      setmode("dark");
      document.body.style.background = "#200b46";
    } else {
      setmode("light");
      document.body.style.background = "white";
    }
  };
  const [mode, setmode] = useState("light");
  return (
    <>
      <Router>
        {" "}
        <NavBar Curmode={mode} ToggleMode={togglebtn} />
        <Routes>
                           
          <Route exact path="/" element={<Textviwe Curmode={mode} />}></Route>
                           
          <Route exact path="/About" element={<About />}></Route>
                    
        </Routes>
      </Router>
    </>
  );
}
export default App;
